<template>
  <el-row v-loading.fullscreen.lock="loadingPage">
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="24">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="12">
              <h3>名单操作日志</h3>
            </el-col>
            <el-col :span="12" class="text-align-right">

            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <div class="margin-4x"></div>
      <el-row class="text-align-right">
        <div class="domain-search-input">
          <el-input placeholder="请输入关键词进行搜索" prefix-icon="el-icon-search" v-model="dataSearch" @input="onChangeSearch">
          </el-input>
        </div>
        
      </el-row>
      <div class="demo-block">
        <el-table :data="tableData" style=" width: 100%">
          <el-table-column prop="name_list_name" label="名单名称"></el-table-column>
          <el-table-column prop="name_list_item" label="名单条目"></el-table-column>
          <el-table-column prop="name_list_item_action_ip" label="来源IP"></el-table-column>
          <el-table-column prop="name_list_item_action_time" label="时间"></el-table-column>
          <el-table-column prop="name_list_item_action" label="执行动作"></el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="count" :page-size="50"
          @current-change="onCurrentChange" v-model:currentPage="now_page" />
      </div>
    </el-col>
  </el-row>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";

export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: true,
      dialogNameListItemFormVisible: false,
      loading: false,

      tableData: [],
      count: 0,
      now_page: 1,
      dataSearch: "",
    };
  },
  computed: {
    rules() {
      return {
        
      }
    }
  },
  
  mounted() {
    this.getData(1);
  },
  methods: {
    getData(nowPage) {
      var t = this;
      JXAjax(
        "post",
        "/report/get_name_list_item_action_log",
        { page: nowPage },
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.count = response.data.count;
          t.now_page = response.data.now_page;
          
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onCurrentChange() { 
      if (this.dataSearch == "") {
        this.getData(this.now_page)
      } else { 
        this.onChangeSearch()
      }
      
    },
    onChangeSearch() { 
      var t = this;
      if (t.dataSearch) {
        JXAjax(
          "post",
          "/waf/waf_search_sys_name_list_item",
          { name_list_uuid: t.uuid, page: t.now_page, search_value: t.dataSearch },
          function (response) {
            t.loadingPage = false;
            t.tableData = response.data.message;
            t.count = response.data.count;
            t.now_page = response.data.now_page;
            t.tableData.forEach(item => {
              item.isVisiblePopover = false;
            })
          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else { 
        t.getData(1)
      }
      
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}
.domain-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}
</style>
